<template>
  <div class="max-h-full bg-white px-6 overflow-auto grid dark:bg-gray-800 pb-3 mb-4 md:mb-0">
    <!-- Section Hero  -->
    <div class=" container mt-5 md:mt-0 md:space-x-10 md:grid grid-cols-3 md:gap-10 lg:gap-0 justify-center md:py-20">
      <div class="grid gap-10 justify-center items-center order-1 col-span-1">
        <img data-aos="zoom-in" data-aos-duration="2000" class="lg:h-72 md:h-56 h-40 rounded-full relative z-10" src="../assets/firman.jpeg" alt="foto profile firmanmimang"/>
      </div>
      <div data-aos="fade-in" data-aos-duration="1000" class="mt-8 md:mt-0 lg:justify-end col-span-2">
        <h1 v-if="windowInnerWidth >= 576" class="text-4xl text-gray-800 text-center md:text-left font-bold mb-6 dark:text-white">
          Hi, I am Firman 👋,
          <span class="relative">
            <span className="h-20 pt-2 overflow-x-hidden whitespace-nowrap text-brand-accent">
              Fullstack Web Developer
            </span>
            <span class="border-l-4 absolute -bottom-0 left-0 -top-1 inline-block bg-white dark:bg-gray-800 w-full animate-type-fullstack-developer will-change"></span>
          </span>
        </h1>
        <h1 v-else class="text-4xl text-gray-800 text-center md:text-left font-bold mb-6 dark:text-white">
          Hi, I am Firman, Fullstack Web Developer
        </h1>
        <p class="mb-2 text-xl leading-relaxed text-gray-800 text-center md:text-justify dark:text-white">
          I’ d love to bring your ideas to life. I have a passion for creating
          intuitive responsive web application for projects both large and small
          that work on any type and size of device.
          Whatever project you have in mind do get in touch and we will work on
          it together. I am available for freelance, contract or direct client
          projects.
        </p>
        <a @click.prevent="openPopup" ref="quoteRef" class="text-xl block text-center md:text-left text-indigo-400 cursor-pointer font-semibold hover:text-indigo-500 transition-colors duration-300 dark:text-indigo-600 dark:hover:text-indigo-700">so do drop me a message.</a>
        <button @click="downloadResume" class="flex items-center justify-center mt-6 mx-auto md:mx-0 text-xl py-3 px-4 text-gray-100 font-semibold rounded capitalize transition duration-300 bg-gray-800 hover:bg-gray-900 dark:bg-gray-100 dark:text-gray-900 dark:hover:bg-gray-300 dark:focus:bg-gray-400">
          Download Resume<template v-if="loading">&nbsp;<svg class="h-5 w-auto" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto; background: none; display: block; shape-rendering: auto;" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
              <circle v-if="dark == 'dark'" cx="50" cy="50" r="44" stroke-width="9" stroke="#1f2937" stroke-dasharray="69.11503837897544 69.11503837897544" fill="none" stroke-linecap="round">
                <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="0.7042253521126761s" keyTimes="0;1" values="0 50 50;360 50 50"></animateTransform>
              </circle>
              <circle v-else cx="50" cy="50" r="44" stroke-width="9" stroke="#f3f4f6" stroke-dasharray="69.11503837897544 69.11503837897544" fill="none" stroke-linecap="round">
                <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="0.7042253521126761s" keyTimes="0;1" values="0 50 50;360 50 50"></animateTransform>
              </circle>
            </svg>
          </template>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { useMeta } from 'vue-meta'

export default {
  props: ['linkMessage'],
  name: "Home",
  setup () {
    useMeta({ title: 'Home' })
  },
  data(){
    return{
      loading: false,
    }
  },
  computed: {
    dark(){
      return this.$store.getters.mode
    },
    windowInnerWidth(){
      return window.innerWidth
    },
  },
  methods: {
    openPopup(){
      // emit event to global untuk di tangkap di contact popup component
      !this.$store.getters.openPopup && this.emitter.emit("toggle-contactPopup", this.$store.getters.openPopup)
      if(this.windowInnerWidth > 1024){
        this.$store.commit('openViaLink')
      } else {
        this.$router.push({name: 'contact'})
      }
    },
    downloadResume(){
      this.loading = true
      window.location.replace('https://drive.google.com/uc?export=download&id=1CKhf4ZhFX9NPSfE5sd3XQ1-2D8sKypXM')
      setTimeout(() => {
        this.$toast.open({
          message: "I hope that on consideration of my Resume, you will look into my potential to be part of your team.",
          type: "success",
          duration: 6000,
          queue: true,
          dismissible: true,
          position: 'bottom-left'
        })
        this.loading = false
      }, 4000)
    }
  },
};
</script>

<style scoped>
.cursor::after {
  display: block;
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  background-color: #fff;
  animation: cursor 0.6s linear infinite alternate;
  will-change: opacity;
}

@keyframes cursor {
  0%,
  40% {
    opacity: 1;
  }

  60%,
  100% {
    opacity: 0;
  }
}
</style>